import NavTop from "./NavTop";
import { serverBus } from "@/main";
import Activations from "./Activation/";
import { NavCommon } from "@/mixins/";
import UsersSelect from "@/components/UsersSelect";

import _ from "lodash";
import Paginate from "vuejs-paginate";

export default {
  name: "invoices-list",
  props: {
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: true
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  components: {
    NavTop,
    Activations,
    Paginate,
    UsersSelect
  },
  mixins: [NavCommon],
  data() {
    return {
      items: [],
      fields: [
        { key: "id", sortable: true, label: "ID" },
        { key: "username" },
        { key: "price" },
        { key: "value", label: "Total" },
        { key: "status" },
        { key: "failed", label: "Failed activation", tdClass: "text-center" },
        {
          key: "created_at",
          label: "Created",
          sortable: true,
          thClass: "text-right",
          tdClass: "text-right"
        },
        {
          key: "action",
          label: "Action",
          thClass: "text-right",
          tdClass: "text-right"
        }
      ],
      filter: null,
      loading: false,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100, 250, 500, 1000],
      sortBy: "status",
      sortDesc: false,
      totalItems: 0,
      pageCount: 1,
      perPage: 100,
      options: [],
      page: 1,
      selectedUsername: "",
      status: "pending"
    };
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key };
        });
    },
    propUsername() {
      return this.selectedUsername ? `&username=${this.selectedUsername}` : "";
    }
  },
  created() {
    this.status = this.$route.params.status;
    this.listInvoices();
    serverBus.$on("invoices-fetch", () => {
      this.listInvoices();
    });
  },
  beforeDestroy() {
    serverBus.$off("invoices-fetch");
  },
  watch: {
    "$route.params.status"() {
      this.status = this.$route.params.status;
      this.listInvoices();
    },
    selectedUsername() {
      this.listInvoices();
    }
  },
  methods: {
    listInvoices(pageNo = 1) {
      this.loading = true;
      this.$http(
        "/api/v1/admin/invoices?per_page=" +
          this.perPage +
          "&page=" +
          pageNo +
          "&status=" +
          this.status +
          this.propUsername,
        {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        }
      )
        .then((response) => {
          this.items = response.data;
          this.totalItems = parseInt(response.headers.total);
          this.perPage = parseInt(response.headers["per-page"]);
          this.pageCount = Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          );
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    },
    nextPageCall(pageNum) {
      this.listInvoices(pageNum);
    },
    deleteInvoice(item) {
      if (this.validateDelete()) {
        this.loading = true;
        this.$http({
          url: "/api/v1/admin/invoices/" + item.uuid,
          data: this.form,
          method: "DELETE",
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        })
          .then((response) => {
            if (response.status == 200) {
              this.$toasted.show("Invoice successfully destroyed", {
                position: "top-right",
                duration: 5000,
                type: "danger",
                closeOnSwipe: true
              });
              this.loading = false;
              this.listInvoices();
            }
          })
          .catch((error) => {
            error.response.data.map((m) => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true
              });
            });
            this.loading = false;
          });
      }
    },
    openDetails(item) {
      serverBus.$emit("invoices-modal-activation", item);
    },
    setPerPage(val) {
      this.perPage = val;
      this.listInvoices();
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
