<template>
  <b-modal
    ok-only
    size="xl"
    id="modalVariants"
    :title="' Invoice - ' + node.data.id"
    @hide="reset"
  >
    <form>
      <div class="row">
        <span class="col-12">
          <label for>Transaction ID (TXID)</label>
          <input
            type="text"
            v-model="node.data.txidIncoming"
            class="form-control"
            name
          />
        </span>

        <span class="col-12">
          <label for>Confirmation</label>
          <input
            type="number"
            v-model="node.data.confirmations"
            class="form-control"
            name
          />
        </span>

        <span class="col-12">
          <label for>Amount received</label>
          <input
            type="number"
            v-model="node.data.amount_received"
            class="form-control"
            name
          />
        </span>
      </div>
      <hr />
      <div class="row">
        <span class="col-12">
          <label for>Wallet</label>
          <input
            type="text"
            v-model="node.data.wallet"
            class="form-control"
            name
            disabled
          />
          <small>
            <a
              v-if="node.data.currency.code === 12"
              target="_blank"
              :href="
                'https://www.blockchain.com/btc/address/' + node.data.wallet
              "
              >CHECK ON BLOCKCHAIN HERE!</a
            >
            <a
              v-if="node.data.currency.code === 13"
              target="_blank"
              :href="
                'https://live.blockcypher.com/ltc/address/' + node.data.wallet
              "
              >CHECK ON BLOCKCHAIN HERE!</a
            >
          </small>
        </span>

        <span class="col-12 mt-3">
          <label for>
            <h2>
              Price:
              <span class="badge badge-danger"
                >{{ node.data.total_crypto }}
                {{ node.data.currency ? node.data.currency.name : 0 }}</span
              >
            </h2>
          </label>
        </span>

        <span class="col-12">
          <label for>
            <strong
              >Plan price: ${{
                node.data.plan && node.data.plan.price
              }}
              USD</strong
            >
          </label>
        </span>
      </div>
      <div v-if="node.data.blockchain_txs">
        <label for>Wallet transactions:</label>
        <div class="highlight">
          <vue-json-pretty
            highlightMouseoverNode
            :path="'res'"
            :data="node.data.blockchain_txs"
          ></vue-json-pretty>
        </div>
      </div>
      <hr />

      <b-row>
        <b-form-group class="col-12" label-sr-only>
          <b-form-checkbox-group stacked>
            <div class="custom-control custom-switch">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk11"
                v-model="asign_comissions"
              />
              <label class="custom-control-label" for="customChk11">
                <span
                  class="ml-5  font-weight-bold"
                  :class="(asign_comissions && 'text-success') || 'text-danger'"
                >
                  {{ (asign_comissions && "YES") || "NO" }}
                </span>
                <span class="ml-5"> Skip Direct & Residual Reward</span>
              </label>
            </div>
          </b-form-checkbox-group>
          <b-form-checkbox-group stacked>
            <div class="custom-control custom-switch">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk15"
                v-model="skip_binary_comissions"
              />
              <label class="custom-control-label" for="customChk15">
                <span
                  class="ml-5  font-weight-bold"
                  :class="
                    (skip_binary_comissions && 'text-success') || 'text-danger'
                  "
                >
                  {{ (skip_binary_comissions && "YES") || "NO" }}
                </span>
                <span class="ml-5"> Skip Binary Points</span>
              </label>
            </div>
          </b-form-checkbox-group>

          <b-form-checkbox-group stacked>
            <div class="custom-control custom-switch">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk13"
                v-model="daily_disabled"
              />
              <label class="custom-control-label" for="customChk13">
                <span
                  class="ml-5  font-weight-bold"
                  :class="(daily_disabled && 'text-success') || 'text-danger'"
                >
                  {{ (daily_disabled && "YES") || "NO" }}
                </span>
                <span class="ml-5">
                  Skip Daily Reward
                </span>
              </label>
            </div>
          </b-form-checkbox-group>

          <b-form-checkbox-group stacked>
            <div class="custom-control custom-switch">
              <input
                type="checkbox"
                class="custom-control-input"
                id="customChk12"
                v-model="corp_activation"
              />
              <label class="custom-control-label" for="customChk12">
                <span
                  class="ml-5  font-weight-bold"
                  :class="(corp_activation && 'text-success') || 'text-danger'"
                >
                  {{ (corp_activation && "YES") || "NO" }}
                </span>
                <span class="ml-5">Corporative Activation</span>
              </label>
            </div>
          </b-form-checkbox-group>
        </b-form-group>
      </b-row>
    </form>

    <div slot="modal-footer">
      <button
        type="button"
        class="btn btn-dark mr-2"
        data-dismiss="modal"
        @click="hideModal()"
      >
        Close
      </button>
      <button
        type="button"
        v-if="
          node.data.invoice_status && node.data.invoice_status === 'pending'
        "
        class="btn btn-success mr-1"
        @click.prevent="updateInvoice"
        :disabled="loading"
      >
        Confirm
      </button>
    </div>
  </b-modal>
</template>

<script>
import { serverBus } from "@/main";

import VueJsonPretty from "vue-json-pretty";

export default {
  name: "invoice-activation-modal",
  data() {
    return {
      loading: false,
      show: true,
      asign_comissions: true,
      corp_activation: true,
      daily_disabled: true,
      big_pro_account: true,
      skip_binary_comissions: true,
      node: {
        delivery_attempt: 1,
        data: {
          txidIncoming: null,
          txid: null,
          status: Object,
          currency: Object,
          invoice_status: Object
        }
      }
    };
  },
  watch: {
    "node.data.txidIncoming": function(newVal) {
      this.node.data.txid = `${this.$store.getters.currentUser.username}-${newVal}`;
    }
  },
  components: {
    VueJsonPretty
  },
  created() {
    serverBus.$on("invoices-modal-activation", (node) => {
      this.node.data = node;
      this.node.data.address = node.wallet;
      this.node.data.txidIncoming = node.txid;
      this.node.data.blockchain_txs =
        node.blockchain_txs && JSON.parse(node.blockchain_txs);
      this.loading = false;
      this.showModal();
    });
  },
  beforeDestroy() {
    serverBus.$off("invoices-modal-activation");
  },
  methods: {
    updateInvoice() {
      this.loading = true;
      this.setOptions();
      this.$http({
        url: "/api/v1/client/invoices/activation",
        data: this.node,
        method: "PUT",
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then((response) => {
          this.$toasted.show("Invoice successfully updated", {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true
          });
          this.loading = false;
          this.hideModal();
          serverBus.$emit("invoices-fetch");
        })
        .catch((error) => {
          this.loading = false;
          this.$toasted.show("Network error", {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true
          });
        });
    },
    showModal() {
      this.$root.$emit("bv::show::modal", "modalVariants");
    },
    hideModal() {
      this.$root.$emit("bv::hide::modal", "modalVariants");
    },
    setOptions() {
      this.node.data.asign_comissions = this.asign_comissions;
      this.node.data.corp_activation = this.corp_activation;
      this.node.data.daily_disabled = this.daily_disabled;
      this.node.data.big_pro_account = this.big_pro_account;
      this.node.data.skip_binary_comissions = this.skip_binary_comissions;
    },
    reset() {
      this.asign_comissions = true;
      this.corp_activation = true;
      this.daily_disabled = true;
      this.big_pro_account = true;
      this.skip_binary_comissions = true;
    }
  }
};
</script>
